@import '../../assets/styles/mixins/section-container';

.root {
  background-color: #fff;
}

.container {
  @include section-container(96, 120, 96);

  @media screen and (max-width: 1360px) {
    padding-inline: 70px;
  }

  @media screen and (max-width: 1243px) {
    padding-inline: 50px;
  }

  @media screen and (max-width: 1006px) {
    padding: 81px 30px 80px;
  }

  @media screen and (max-width: 560px) {
    padding: 70px 19px 70px 20px;
  }
}

.list {
  list-style: none;
  display: flex;
  gap: 30px;
  margin: 39px 0 0;
  padding: 0;

  @media screen and (max-width: 986px) {
    gap: 20px;
    margin-top: 40px;
  }

  @media screen and (max-width: 531px) {
    flex-direction: column;
    gap: 30px;
    margin-top: 30px;
  }
}

.listItem {
  @media screen and (max-width: 986px) {
    width: 31.83%;
  }

  @media screen and (max-width: 531px) {
    width: 100%;
  }
}

.listItemTypeSecond {
  @media screen and (max-width: 986px) {
    width: 31.39%;
  }
  @media screen and (max-width: 531px) {
    width: 100%;
  }
}

.listItemTypeThird {
  @media screen and (max-width: 986px) {
    width: 30.22%;
  }
  @media screen and (max-width: 531px) {
    width: 100%;
  }
}
