.root {
  width: 28px;
  height: 18px;
  background: none;
  border: none;
  margin: 0 0 2px;
  padding: 0 0 0.5px 0;
  cursor: pointer;
  display: none;

  @media screen and (max-width: 884px) {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
  }
}

.line {
  width: 100%;
  height: 1.5px;
  background-color: #045c5e;
}
